// Typography
body {
    font-family: 'Lato', sans-serif;
    font-size: 12px;
    line-height: 1.2;
    color: var(--grey-200);
    font-weight: 400;
}

    h2 {
        color: var(--dark);
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.025em;
    }

h3 {
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: var(--blue);
}

h4 {
    font-size: 15px;
    font-weight: 700;
    line-height: 2;
    letter-spacing: 0.025em;
    color: var(--grey-100);
}

.btn {
    display: inline-block;
    border-radius: 2px;
    padding: 8px 30px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 0.1em;
    background-color: var(--grey-400);
    color: var(--grey-200);
    border: 1px solid var(--grey-400);
    cursor: pointer;

    &:hover {
        background-color: transparent;
    }

}

.input-message {
    font-family: 'Lato', sans-serif;
    font-size: 12px;
    line-height: 1.2;
    color: var(--grey-200);
    font-weight: 400;
}